/** Automatically convert popular OCG terms into their TCG counterpart. Never introduce machine translation here :'( */
export const ocgToTCGTermMap: Record<string, string> = {
    /** monster type */
    '': 'Aqua',
    '': 'Beast',
    '': 'Beast-Warrior',
    '': 'Creator God',
    '': 'Cyberse',
    '': 'Dinosaur',
    '': 'Divine-Beast',
    '': 'Dragon',
    '': 'Fairy',
    '': 'Fiend',
    '': 'Fish',
    '': 'Galaxy',
    '': 'Illusion',
    '': 'Insect',
    '': 'Machine',
    '': 'Plant',
    '': 'Psychic',
    '': 'Pyro',
    '': 'Reptile',
    '': 'Rock',
    '': 'Sea Serpent',
    '': 'Spellcaster',
    '': 'Thunder',
    '': 'Warrior',
    '': 'Winged Beast',
    '': 'Wyrm',
    '': 'Zombie',
    '': 'Cyborg',
    '': 'Celestial Warrior',
    '': 'High Dragon',
    '': 'Magical Knight',
    '': 'Omega Psychic',
    /** type */
    '': 'Monster',
    '': 'Spell Card',
    '': 'Trap Card',
    '': 'Action',
    /** subType */
    '': 'Normal',
    '': 'Effect',
    '': 'Ritual',
    '': 'Fusion', // This is Fusion in Rush Duel, when we translate this map into TCG one these two entries will have the same "Fusion" key. We place this one above so it will got overwritten by the normal OCG text for Fusion in the master series.
    '': 'Fusion',
    '': 'Synchro',
    '': 'Xyz',
    '': 'Pendulum',
    '': 'Link',
    '': 'Maximum',
    '': 'Token',
    '': 'Special Summon',
    '': 'Dark Synchro',
    /** ability */
    '': 'Tuner',
    '': 'Gemini',
    '': 'Flip',
    '': 'Toon',
    '': 'Spirit',
    '': 'Union',
    /** creator */
    ' ': '1996 KAZUKI TAKAHASHI',
    'KONAMI': '2020 Studio Dice/SHUEISHA, TV TOKYO, KONAMI',
    /** sample card */
    '{|}{|}': 'Junko',
    [`
(`]: `[2+ Effect Monsters on your field]
Each time an opponent's monster activates its effect, place 1 Pure Counter on that opponent's monster (max. 1) after that effect resolves. Unaffected by activated effects from monster with a Pure Counter. Cannot be destroyed by battle with monsters with a Pure Counter.`,
    // eslint-disable-next-line quotes
    '1': "Once per turn: You can pay 800 LP, increase this card's Pendulum Scale by 1.",
};
export const tcgToOCGTermMap = Object.entries(ocgToTCGTermMap).reduce((acc, cur) => {
    const [ocgTerm, tcgTerm] = cur;
    acc[tcgTerm] = ocgTerm;

    return acc;
}, {
    /** Safe fallback, because the single quote maybe converted into curly single quote */
    'Once per turn: You can pay 800 LP, increase this cards Pendulum Scale by 1.': '1',
} as Record<string, string>);